<template>
  <v-container id="container">
    <!-- Saldo atual -->
    <Balance />

    <!-- Barra de opes: filtros, modos de visualizao e opo de novo cadastro -->
    <Toolbar />

    <!-- Mensagem de alerta com o resultado da ltima operao realizada -->
    <transition mode="out-in" name="slide-fade">
      <v-alert
        :type="resultOperation.type"
        class="mt-4"
        icon="mdi-alert"
        outlined
        prominent
        v-if="resultOperation.show"
        >{{ resultOperation.message }}
      </v-alert>
    </transition>

    <!-- Mostra as contas encontradas de acordo com o filtro de pesquisa e o modo de visualizao selecionado pelo usurio -->
    <transition mode="out-in" name="slide-fade" v-if="bills.length">
      <!-- Visualizao no formato de lista -->
      <BillsList class="mt-4" v-if="exhibitionMode === 'list'" />

      <!-- Visualizao no formato de grfico -->
      <BillsChart class="mt-4" v-else-if="exhibitionMode === 'chart'" />
    </transition>

    <!-- Mensagem de alerta de nenhum registro encontrado -->
    <v-alert
      type="warning"
      class="mt-4"
      icon="mdi-alert"
      outlined
      prominent
      v-else
      >Nenhum registro encontrado.
    </v-alert>

    <!-- Dilogo com os filtros de pesquisa -->
    <FiltersDialog />

    <!-- Dilogo para cadastro/alterao de uma conta -->
    <EditDialog />

    <!-- Dilogo para excluso de uma conta -->
    <DeleteDialog />
  </v-container>
</template>

<style scoped>
/* Tamanho mximo do container da aplicao */
.container {
  max-width: 960px !important;
}
</style>

<script>
import { computed } from "@/computed";
import Balance from "@/components/Balance";
import BillsChart from "@/components/BillsChart";
import BillsList from "@/components/BillsList";
import DeleteDialog from "@/components/dialog/DeleteDialog";
import EditDialog from "@/components/dialog/EditDialog";
import FiltersDialog from "@/components/dialog/FiltersDialog";
import Toolbar from "@/components/Toolbar";

export default {
  mixins: [computed],
  components: {
    Balance,
    BillsChart,
    BillsList,
    DeleteDialog,
    EditDialog,
    FiltersDialog,
    Toolbar,
  },
  mounted() {
    // Ao carregar a pgina, busca o saldo atual e as contas do ms atual
    this.$store.dispatch("fetchBalance");
    this.$store.dispatch("fetchBills");
  },
};
</script>
